.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.g
{
  border-radius: 1000px;
}
.App-footer {
  background-color: #1b1d22;
  height: 200px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}











.bigBox {
    display: flex;
    flex-direction: column; /* Adjusted to column for consistency with below media query */
    gap: 0px;
    align-items: center;
}

.container {
  display: flex;
  flex-direction: row; /* or column, depending on your layout */
  margin-left: 20%;
  margin-right: 20%;
  align-items: center; /* This ensures vertical alignment is centered */
  gap: 20px;
}

.back{
  background-position: center; /* Center the background image */
  background-size: cover; /* Cover the entire screen */
  background-repeat: no-repeat; /* Do not repeat the background image */
  min-height: 100vh; /* Minimum height of 100% of the viewport height */
  width: 100%; /* Width of 100% */
  display: flex;
}

.smaller {
  display: flex;
  flex-direction: column;
  padding: 0;
  justify-content: flex-start;
  align-items: flex-start; /* Align content to the left */
}

.title {
  font-size: 25pt;
}
.image {
  height: 200px;
  width: 300px;
  border-radius: 45px;
  margin: 0;
  position: relative;
}

.quote {
  font-size: 10pt;
}

.text {
  font-size: 10pt;
}

.hey
{
  font-size: 30pt;
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 40%;
}
.heys
{
  font-size: 50pt;
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 39%;
}
.subtitle {
  font-size: 15pt;
}

p {
  margin: 0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    flex-direction: column; /* Stack children vertically on small screens */
    align-items: center; /* Center align the items for a better mobile experience */
  }
  
  .title, .subtitle, .text, .quote {
  }
  .smaller
  {
    position: relative;
    top: -20px;
   
    align-items: stretch;
  }
}

